.sidebar{
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarItem{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarTitle{
    margin-top: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a8b4b4;
    border-bottom: 1px solid #a8b4b4;
    font-family: 'Tiro Bangla', serif;
    font-size: 14px;
    color: #141b1b;
    line-height: 20px;
    text-align: center;
}

.sidebarItem > img{
    margin-top: 15px;
    width: 90%;
    height: 50%;
}

.sidebarItem > p{
    padding: 30px;
}

.sidebarList{
    list-style: none;
    margin-bottom: 30px;
}

.sidebarListItem{
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}

.sidebarSocial{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebarIcon{
    font-style: 15px;
    margin-left: 15px;
}
